<template>
  <v-card
    v-if="activeDispatcher"
    width="100%"
    :height="$vuetify.breakpoint.mobile ? '100%' : 'fit-content'"
    class="user-card py-lg-5 py-md-12 pt-12 pb-7 mx-lg-2 mx-md-2 mx-5 px-lg-5 px-sm-12"
    :flat="$vuetify.breakpoint.mobile"
  >
    <div class="dispatcher-card d-flex flex-column">
      <v-row
        class="justify-space-between"
        :class="{ 'flex-grow-0': $vuetify.breakpoint.mobile }"
        no-gutters
      >
        <div class="d-flex align-lg-center align-start flex-wrap flex-lg-row flex-column">
          <h2 class="primary-dark--text mr-4">
            {{ activeDispatcher.name }}
          </h2>

          <!-- TODO: the name should be rewritten to some common one, for example status -->
          <status-load
            v-if="activeDispatcher.status"
            :status="activeDispatcher.status"
          />
          <!-- END TODO: the name should be rewritten to some common one, for example status -->
        </div>
        <template v-if="showDispatcherControls">
          <v-col
            v-if="!$vuetify.breakpoint.mobile"
            cols="auto"
          >
            <v-btn
              v-if="activeDispatcher.status === 'Active'"
              width="100"
              :height="$vuetify.breakpoint.mobile ? 50 : 35"
              depressed
              class="mx-1 primary--text btn-background-grey custom__btn"
              :to="{ name: $constants.ROUTE_NAMES.DISPATCHER_EDIT, params: { id: activeDispatcher.id } }"
            >
              Edit
            </v-btn>

            <v-btn
              v-if="activeDispatcher.status === 'Active'"
              :min-width="100"
              :height="$vuetify.breakpoint.mobile ? 50 : 35"
              depressed
              class="mx-1 error--text custom__btn"
              @click="deactivateModalOpened = true"
            >
              Deactivate
            </v-btn>

            <v-btn
              v-if="activeDispatcher.status === 'Deactivated'"
              :min-width="100"
              :height="$vuetify.breakpoint.mobile ? 50 : 35"
              depressed
              class="mx-1 primary--text custom__btn"
              @click="activateDispatcher"
            >
              Reactivate
            </v-btn>

            <v-btn
              v-if="activeDispatcher.status !== 'Active'"
              width="100"
              :height="$vuetify.breakpoint.mobile ? 50 : 35"
              depressed
              class="mx-1 error--text btn-background-grey custom__btn"
              @click="deleteModalOpened = true"
            >
              Delete
            </v-btn>
          </v-col>
        </template>
      </v-row>

      <v-divider
        v-if="$vuetify.breakpoint.mobile"
        class="mt-2"
      />

      <div
        v-if="activeDispatcher.email"
        class="body-2 secondary--text pt-2 mb-lg-n5 mb-0"
        :class="[ $vuetify.breakpoint.mobile ? 'font-weight-normal' : 'font-weight-medium' ]"
      >
        <div class="d-flex align-center flex-nowrap my-5">
          <div class="mr-4 d-flex justify-center align-center">
            <v-img
              alt="email"
              :max-width="24"
              :src="require('@images/icons/drivers/email.svg')"
            />
          </div>

          <p>{{ activeDispatcher.email }}</p>
        </div>

        <div
          v-if="activeDispatcher.phone"
          class="d-flex align-center flex-nowrap my-5"
        >
          <div class="mr-4 d-flex justify-center align-center">
            <v-img
              alt="phone"
              :max-width="24"
              :src="require('@images/icons/drivers/phone.svg')"
            />
          </div>

          <p>{{ activeDispatcher.phone }}</p>
        </div>
      </div>

      <div
        v-if="$vuetify.breakpoint.mobile && showDispatcherControls"
        class="d-flex full-width mt-auto mx-n3 pa-0 align-self-center mobile-fixed-buttons"
      >
        <v-btn
          v-if="activeDispatcher.status === 'Active'"
          height="50"
          depressed
          class="flex-grow-1 flex-shrink-0 mx-3 primary--text btn-background-grey font-weight-bold xs-computed-btn"
          :to="{ name: $constants.ROUTE_NAMES.DISPATCHER_EDIT, params: { id: activeDispatcher.id } }"
        >
          Edit
        </v-btn>

        <v-btn
          v-if="activeDispatcher.status !== 'Active'"
          height="50"
          depressed
          class="flex-grow-1 flex-shrink-0 mx-3 color-red white--text font-weight-bold xs-computed-btn"
          @click="deleteModalOpened = true"
        >
          Delete
        </v-btn>

        <v-btn
          v-if="activeDispatcher.status === 'Active'"
          height="50"
          depressed
          class="flex-grow-1 flex-shrink-0 mx-3 error--text btn-background-grey font-weight-bold xs-computed-btn"
          @click="deactivateModalOpened = true"
        >
          Deactivate
        </v-btn>

        <v-btn
          v-if="activeDispatcher.status === 'Deactivated'"
          height="50"
          depressed
          class="flex-grow-1 flex-shrink-0 mx-3 primary--text font-weight-bold xs-computed-btn"
          @click="activateDispatcher"
        >
          Reactivate
        </v-btn>
      </div>
    </div>

    <modal
      max-width="500"
      :modal-opened="deleteModalOpened"
      @modal:close="deleteModalOpened = false"
    >
      <div class="d-flex flex-column align-center pb-6">
        <h1 class="mb-5 fs-28 text-center">
          Do you want to delete {{ activeDispatcher.name }}
        </h1>

        <div class="d-flex">
          <v-btn
            width="130"
            height="50"
            depressed
            class="flex-grow-1 primary--text btn-background-grey font-weight-bold mx-3  custom__btn"
            @click="deleteModalOpened = false"
          >
            Cancel
          </v-btn>

          <v-btn
            width="130"
            height="50"
            depressed
            class="flex-grow-1 red-light danger--text font-weight-bold mx-3 custom__btn"
            @click="deleteDispatcher"
          >
            Delete
          </v-btn>
        </div>
      </div>
    </modal>

    <modal
      max-width="500"
      :modal-opened="deactivateModalOpened"
      @modal:close="deactivateModalOpened = false"
    >
      <div class="d-flex flex-column align-center pb-6">
        <h1 class="mb-5 fs-28 text-center">
          Are you sure you want to deactivate {{ activeDispatcher.name }}
        </h1>

        <div class="d-flex">
          <v-btn
            width="130"
            height="50"
            depressed
            class="flex-grow-1 primary--text btn-background-grey font-weight-bold mx-3"
            @click="deactivateModalOpened = false"
          >
            No
          </v-btn>

          <v-btn
            width="130"
            height="50"
            depressed
            class="flex-grow-1 danger--text red-light font-weight-bold mx-3"
            @click="deactivateDispatcher"
          >
            Yes
          </v-btn>
        </div>
      </div>
    </modal>
  </v-card>
</template>

<script>
import Modal from '@components/Modal/Modal.vue';
import StatusLoad from '@components/Loads/StatusLoad/StatusLoad.vue';
import ROLES from '@utils/constants/roles';
import { createNamespacedHelpers, mapState } from 'vuex';
import * as constants from '@modules/dispatchers/constants';
import EventBus from '@utils/eventBus';

const { mapGetters, mapActions } = createNamespacedHelpers('dispatchers');

export default {
  name: 'Dispatcher',
  components: {
    Modal,
    StatusLoad,
  },
  data: () => ({
    deleteModalOpened: false,
    deactivateModalOpened: false,
  }),
  computed: {
    showDispatcherControls() {
      return this.userRole === ROLES.CARRIER;
    },
    ...mapState(['userRole']),
    ...mapGetters({
      activeDispatcher: constants.getters.ACTIVE_DISPATCHER,
    }),
  },
  methods: {
    ...mapActions({
      changeDispatcherStatus: constants.actions.CHANGE_DISPATCHER_STATUS,
      deleteActiveDispatcher: constants.actions.DELETE_DISPATCHER,
    }),
    deactivateDispatcher() {
      this.changeDispatcherStatus({ id: this.activeDispatcher.id, data: { status: 'deactivated' } })
        .then(() => {
          this.deactivateModalOpened = false;
          this.$notificationSuccess('Dispatcher was deactivated');
        })
        .catch(() => { this.$notificationError('Dispatcher wasn\'t deactivated'); });
    },
    activateDispatcher() {
      this.changeDispatcherStatus({ id: this.activeDispatcher.id, data: { status: 'active' } })
        .then(() => {
          this.$notificationSuccess('Dispatcher was reactivated');
        })
        .catch(() => { this.$notificationError('Dispatcher wasn\'t reactivated'); });
    },
    deleteDispatcher() {
      this.deleteActiveDispatcher({ id: this.activeDispatcher.id })
        .then(() => {
          this.deleteModalOpened = false;
          EventBus.$emit(this.$constants.EVENTS.REDIRECT_EVENT);
          this.$notificationSuccess('Dispatcher was deleted');
        })
        .then(() => {
          this.$router.push({ name: this.$constants.ROUTE_NAMES.DISPATCHERS });
        })
        .catch(() => { this.$notificationError('Dispatcher wasn\'t deleted'); });
    },
  },
};
</script>

<style lang="scss">
.dispatcher-card {
  @media screen and (max-width: 1264px) {
    height: 100%;
  }
}

.v-overlay--active .v-overlay__scrim {
  @media screen and (max-width: 1264px) {
    background: rgba(98, 105, 118, 0.5) !important;
    backdrop-filter: blur(8px) !important;
    opacity: 1 !important;
  }
}
</style>
